const Organization = () => {
  const organizations = [
    {
      organization: "Desa Putra Futsal Team",
      organizator: "Desa Putra",
      duration: "2014",
      status: "Leader",
    },
    {
      organization: "Christmas Lunch",
      organizator: "San Egidio",
      duration: "Aug - Oct 2020",
      status: "Volunteer",
    },
    {
      organization: "OMK St. Theresia",
      organizator: "St. Theresia",
      duration: "2019",
      status: "Member",
    },
  ];

  return (
    <div>
      <h4 className="display-6" style={{ fontSize: 24 }}>
        {" "}
        <i className="fa fa-sitemap fa-lg text-primary mx-2"></i> Organization
      </h4>
      <ul>
        {organizations.map((organization, id) => {
          return (
            <li key={id}>
              <h6 className="display-6" style={{ fontSize: 20 }}>
                {organization.organization}
              </h6>
              <p className="lead" style={{ fontSize: 14 }}>
                <i className="fa fa-building" style={{ marginRight: 5 }}></i>{" "}
                {organization.organizator}
              </p>
              <p className="lead" style={{ fontSize: 14, marginTop: -14 }}>
                <i className="fa fa-clock" style={{ marginRight: 5 }}></i>{" "}
                {organization.duration}
              </p>
              <p className="lead" style={{ fontSize: 14, marginTop: -14 }}>
                <i className="fa fa-user" style={{ marginRight: 5 }}></i>{" "}
                {organization.status}
              </p>
            </li>
          );
        })}
      </ul>
    </div>
  );
};

export default Organization;
