import React, { useState } from "react";
import Welcome from "./components/Welome";
import Profile from "./components/Profile";
import Contact from "./components/Contact";

const App = () => {
  const [page, setPage] = useState("welcome");

  const onClickHandler = (page) => {
    setPage(page);
  };

  console.log(page);
  return (
    <div>
      {page === "welcome" ? <Welcome onClickHandler={onClickHandler} /> : ""}
      {page === "profile" ? <Profile onClickHandler={onClickHandler} /> : ""}
      {page === "contact" ? <Contact onClickHandler={onClickHandler} /> : ""}
    </div>
  );
};

export default App;
