const Award = () => {
  const awards = [
    {
      award: "Second Winner in Futsal Competition",
      issue: "Perum Peruri",
      time: "2018",
    },
    {
      award: "TOP 4 in Football Competition",
      issue: "Perum Peruri",
      time: "2018",
    },
    {
      award: "First Winner in T-Shirt Design Compeition",
      issue: "PA Desa Putra",
      time: "2014",
    },
    {
      award: "First Winner in Painting Compeition",
      issue: "PA Desa Putra",
      time: "2014",
    },
  ];

  return (
    <div>
      <h4 className="display-6" style={{ fontSize: 24 }}>
        {" "}
        <i className="fa fa-trophy fa-lg text-warning mx-2"></i> Awards
      </h4>
      <ul>
        {awards.map((award, id) => {
          return (
            <li key={id}>
              <h6 className="display-6" style={{ fontSize: 20 }}>
                {award.award}
              </h6>
              <p className="lead" style={{ fontSize: 14 }}>
                <i className="fa fa-building" style={{ marginRight: 5 }}></i>{" "}
                {award.issue}
              </p>
              <p className="lead" style={{ fontSize: 14, marginTop: -14 }}>
                <i className="fa fa-clock" style={{ marginRight: 5 }}></i>{" "}
                {award.time}
              </p>
            </li>
          );
        })}
      </ul>
    </div>
  );
};

export default Award;
