const Certification = () => {
  const certifications = [
    {
      title: "Human Reources Management",
      issue: "AHS",
      time: "2020",
    },
    {
      title: "Managerial Skill and Leadership Character Building",
      issue: "Bumi Arasy",
      time: "2017",
    },
  ];

  return (
    <div>
      <h4 className="display-6" style={{ fontSize: 24 }}>
        {" "}
        <i className="fa fa-award fa-lg text-info mx-2"></i> Certification
      </h4>
      <ul>
        {certifications.map((certification, id) => {
          return (
            <li key={id}>
              <h6 className="display-6" style={{ fontSize: 20 }}>
                {certification.title}
              </h6>
              <p className="lead" style={{ fontSize: 14 }}>
                <i className="fa fa-building" style={{ marginRight: 5 }}></i>{" "}
                {certification.issue}
              </p>
              <p className="lead" style={{ fontSize: 14, marginTop: -14 }}>
                <i className="fa fa-clock" style={{ marginRight: 5 }}></i>{" "}
                {certification.time}
              </p>
            </li>
          );
        })}
        <li>
          <h6 className="display-6" style={{ fontSize: 20 }}>
            More ...
          </h6>
        </li>
      </ul>
    </div>
  );
};

export default Certification;
