const Working = (props) => {
  const workings = [
    {
      title: "Acounting Staff",
      company: "PT Perdana Jatiputra",
      duration: "2019 - Now",
      status: "Fulltime",
    },
    {
      title: "Operator Assistant",
      company: "Perum Peruri",
      duration: "2014 - 2019",
      status: "Contract",
    },
    {
      title: "Sales Marketing",
      company: "Neo Racing Sport",
      duration: "2014",
      status: "Self Employed",
    },
  ];

  return (
    <div>
      <h4 className="display-6" style={{ fontSize: 24 }}>
        {" "}
        <i className="fa fa-briefcase fa-lg text-success mx-2"></i> Working
      </h4>
      <ul>
        {workings.map((working, id) => {
          return (
            <li key={id}>
              <h6 className="display-6" style={{ fontSize: 20 }}>
                {working.title}
              </h6>
              <p className="lead" style={{ fontSize: 14 }}>
                <i className="fa fa-building" style={{ marginRight: 5 }}></i>{" "}
                {working.company}
              </p>
              <p className="lead" style={{ fontSize: 14, marginTop: -14 }}>
                <i className="fa fa-clock" style={{ marginRight: 5 }}></i>{" "}
                {working.duration}
              </p>
              <p className="lead" style={{ fontSize: 14, marginTop: -14 }}>
                <i className="fa fa-user" style={{ marginRight: 5 }}></i>{" "}
                {working.status}
              </p>
            </li>
          );
        })}
      </ul>
    </div>
  );
};

export default Working;
